.wrapper {
  width: 200px;
  height: 60px;
  position: absolute;
  top: 90%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.circle {
  width: 20px;
  height: 20px;
  background-color: var(--color-accent);
  transform-origin: 50%;
  border-radius: 50%;
  animation: circle .5s infinite alternate;
  position: absolute;
  left: 15%;
}

@keyframes circle {
  0% {
    height: 5px;
    border-radius: 50px 50px 25px 25px;
    top: 60px;
    transform: scaleX(1.7);
  }

  40% {
    height: 20px;
    border-radius: 50%;
    transform: scaleX(1);
  }

  100% {
    top: 0%;
  }
}

.circle:nth-child(2) {
  animation-delay: .2s;
  left: 45%;
}

.circle:nth-child(3) {
  animation-delay: .3s;
  left: auto;
  right: 15%;
}

.shadow {
  width: 20px;
  height: 4px;
  transform-origin: 50%;
  z-index: -1;
  filter: blur(1px);
  background-color: rgba(0, 0, 0, .5);
  border-radius: 50%;
  animation: shadow .5s infinite alternate;
  position: absolute;
  top: 62px;
  left: 15%;
}

@keyframes shadow {
  0% {
    transform: scaleX(1.5);
  }

  40% {
    opacity: .7;
    transform: scaleX(1);
  }

  100% {
    opacity: .4;
    transform: scaleX(.2);
  }
}

.shadow:nth-child(4) {
  animation-delay: .2s;
  left: 45%;
}

.shadow:nth-child(5) {
  animation-delay: .3s;
  left: auto;
  right: 15%;
}

.wrapper span {
  letter-spacing: 12px;
  color: var(--color-accent);
  font-family: Lato;
  font-size: 20px;
  position: absolute;
  top: 75px;
  left: 15%;
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 10;
  background-color: rgba(0, 0, 0, .475);
  transition: opacity .35s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  transition: opacity .35s cubic-bezier(.4, 0, .2, 1);
}

.modal-movie {
  background-image: radial-gradient(circle at 100% 0, rgba(185, 46, 44, .77) 10%, rgba(25, 0, 255, 0) 50%);
  background-color: var(--color-white);
  min-width: 280px;
  height: 930px;
  border-radius: 4px;
  padding: 48px 20px 40px;
  transition: transform .35s cubic-bezier(.4, 0, .2, 1), -webkit-transform .35s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (max-width: 768px) {
